    #t_4_1.titulo-segundo
      h2 4.1  Fibras textiles

    p.mb-4 Es la materia slida unitaria de cualquier estructura textil. La fibra es cada uno de los filamentos que componen las unidades fundamentales en la fabricacin de hilos, textiles y telas; se considera filamento cuando la longitud de este es superior a su dimetro.
    
