    #t_4_3.titulo-segundo
      h2 4.3 Hilos e hilatura

    p.mb-4 Se denomina hilo al conjunto de fibras textiles, continuas o discontinuas, que se tuercen juntas alcanzando una gran longitud y que es directamente empleado para la fabricacin de tejidos; a su vez, hilatura es el arte de hilar los filamentos para obtener un conjunto de hebras preparadas para el proceso de construccin de hilos y tejidos. 

