    #t_4_4.titulo-segundo
      h2 4.4 Tejidos

    p.mb-4 Es el manufacturado de fibras o hilos que tienen una relacin sustancial entre su longitud, rea y su espesor con suficiente resistencia mecnica para otorgarle una cohesin. Los tejidos tienen una clasificacin que se da en dos grupos, tejidos planos y de punto, tambin se contemplan aquellas no tejidas en las que diversas tcnicas a nivel tecnolgico permiten su impresin, en el siguiente esquema permite observar esta clasificacin:

