    p.mb-4 Las caracterizaciones de los textiles se organizan en funcin de su comportamiento, estn relacionadas con el peso del material desde su estructura, fluidez, expansin y comprensin; a continuacin, se relacionan los aspectos ms relevantes de cada una.
    
    .row.mb-5
      .col-md-12
        LineaTiempoD.color-secundario
          .row(numero="1" titulo="Estructura").justify-content-center.color-acento-contenido
            .col-md-10.mb-4.mb-md-0
              p.text-small Las telas estructuradas proporcionan el cuerpo de la prenda, dan forma por s mismas. La cada de la tela ha de mantenerse en su lugar y no caer cuando se le suelta. Combinar telas en una misma prenda permite generar textura y volumen, es importante tener en cuenta por ello la estructura.
              SlyderA(tipo="a")
                .row.justify-content-center.align-items-center.p-4
                  .col.lg-8
                    img(src='@/assets/curso/tema4/1.png', alt=': La imagen presenta varias fotografas de diferentes textiles planos con estructura: paos, tweed, jacquard y pana')
                
                .row.justify-content-center.align-items-center.p-4
                  .col.lg-8
                    img(src='@/assets/curso/tema4/2.png', alt='La imagen presenta fotografas de diferentes tipos de textiles plastificados recubiertos: lino, oxford, faya, shantung, popelina, tafetn, bengalina y satn.')
        
                .row.justify-content-center.align-items-center.p-4
                  .col.lg-8
                    img(src='@/assets/curso/tema4/3.png', alt='La imagen presenta un mosaico de fotografas de tejidos de alta densidad, en algunas de las fotos se distinguen gotas de agua sobre el tejido.')
        
       
          .row(numero="2" titulo="Fluidez").justify-content-center.color-acento-contenido
            .col-md-10.mb-4.mb-md-0
              p.text-small Estas telas son suaves y con cada, no son capaces de aguantar su propio peso, la fluidez est directamente relacionada con el tipo de hilo utilizado en su fabricacin, son hilos muy torsionados tanto de hebras hiladas como de multifilamento. La cada que da sobre el cuerpo suele ser ligeras y de peso medio.
              SlyderA(tipo="a")
                .row.justify-content-center.align-items-center.p-4
                  .col.lg-8
                    img(src='@/assets/curso/tema4/4.png', alt='La figura presenta fotografas de diferentes tipos de tela que tienen la caracterstica de fluidez: chiffon, gasa, chalis, interlock, georgette, crepe, jersey y malla.')
                
                .row.justify-content-center.align-items-center.p-4
                  .col.lg-8
                    img(src='@/assets/curso/tema4/5.png', alt='La figura presenta fotografas de diferentes tipos de tela: encaje, gnero, velvet/terciopelo, lam, crepe texturizado y genero de punto.')
          

          .row(numero="3" titulo="Expansin").justify-content-center.color-acento-contenido
            .col-md-10.mb-4.mb-md-0
              p.text-small En este grupo se incluye el concepto del plisado, este efecto permite expandir la silueta. El concepto de acolchado se incluye en esta categora, este es un mtodo natural para crear calidez mediante la combinacin de capas de tela, se puede aplicar el acolchado medio y acolchado grueso.
              .row.justify-content-center.align-items-center.p-4
                  .col.lg-8
                    img(src='@/assets/curso/tema4/6.png', alt='La figura presenta fotografas de diferentes tipos de telas que tienen la caracterstica de expansin: acolchado, redecillas, tarlatana, de piel o peluche, tules, organza, plisados, crepre de satn plisados.')
      
          .row(numero="4" titulo="Compresin").justify-content-center.color-acento-contenido
            .col-md-10.mb-4.mb-md-0
              p.text-small El objetivo de este grupo es crear ajuste perfecto a la silueta humana. El grado de elasticidad, depender del tipo y cantidad de hilos elsticos. Suelen encontrarse textiles con combinaciones de expandes, licra y acetato.
              .row.justify-content-center.align-items-center.p-4
                  .col.lg-8
                    img(src='@/assets/curso/tema4/7.png', alt='La figura presenta varias fotografas de textiles que tienen la caracterstica de la compresin: punto canal, pana stretch, power lycra, punto de algodn stretch, doble punto y power net.')

          .row(numero="5" titulo="Entretelas").justify-content-center.color-acento-contenido
            .col-md-10.mb-4.mb-md-0
              p.text-small Son la estructura de la ropa que sostienen y refuerzan reas crticas. Existen varios tipos de entretela y se usan para determinar el tacto, la forma y la durabilidad de la prenda. Entre ellas se encuentra:
              ul.lista-ul--color.mb-4
                li 
                  i.far.fa-dot-circle    
                  | <b>Tejidas: </b>pueden ser de algodn, rayn, lana, polister o mezclas de fibras. Se tejen de la misma forma que las telas de tejido plano. Pueden cortarse al sesgo para tener elasticidad con ngulo de 45.
                li 
                  i.far.fa-dot-circle    
                  | <b>No tejidas: </b>por lo general son hechas de polister, rayn, nylon o una mezcla de diferentes fibras, estas son distribuidas al azar y juntadas por accin qumica o trmica dado que no tienen una direccin de hilo, no se desmecha y pueden ser cortadas en cualquier direccin o ngulo.
                li 
                  i.far.fa-dot-circle    
                  | <b>De punto: </b>se encuentran las de nylon, tienen estiramiento a lo ancho y estabilidad a lo largo, le aade forma y cuerpo a prcticamente cualquier tela tejida o de punto.
            .row.justify-content-center.p-4
              .col-lg-4.col-sm-12
                .row.justify-content-center
                  .col-lg-8.col-sm-7
                    h4.text-verde.p-2.text-center Cmo elegir la entretela adecuada?
                LineaTiempoE.color-pincipal
