                    .col-lg-12.mb-4.mb-lg-0
                      h4 Fabricacin y aplicacin
                      p Las entretelas pueden ser tejidas, no tejidas o de punto. Pueden usarse fusionndose, cosindose.
                  .row
                    .col-lg-12.mb-4.mb-lg-0
                      h4 Elsticas o estables
                      p Las entretelas se catalogan bajo estas dos categoras. 
                      p #[b Las elsticas:] donde se requiere dar forma.
                      p #[b Las estables:] en sitios de la prenda donde se necesite fijar.   
              .col-lg-4.col-sm-12
                LineaTiempoE.color-pincipal
                  .row
                    .col-lg-12.mb-4.mb-lg-0
                      h4 Cuidado
