                    .py-3
                  .row
                    .col-lg-12.mb-4.mb-lg-0
                      h4 Color
                      p El color se transparenta a travs de algunas telas, se debe seleccionar una entretela compatible con el color de la tela exterior.
                .py-2  
              .col-lg-4
                
                LineaTiempoE.color-pincipal
