                    .col-lg-12.mb-4.mb-lg-0
                      h4 PESO
                      p Los pesos de las entretelas varan desde las muy livianas hasta las muy pesadas. Se deben elegir de acuerdo al material a trabajar.
                 
        
    br
    br
    p.mb-4 <b style="font-size:20px">Procesos textiles: </b>los procesos textiles hacen referencia al proceso realizado sobre la fibra, el hilo, la tela o la prenda con el fin de modificar alguna de sus caractersticas como la apariencia, el tacto y el comportamiento. Siendo as, los procesos tienen gran importancia para los acabados textiles, puesto que permiten eliminar o minimizar algunas cualidades que no se desean como parte del producto final ya sea en apariencia, tacto o comportamiento, por lo anterior se tienen en cuenta ciertos tipos de operaciones, entre los cuales estn:
