                p se logran efectos con la aplicabilidad de procesos fsicos, mecnicos, entre ellos calor, presin, humedad, a manera de ejemplo, aqu se encuentran los plisados.

      .col-lg-4.d-flex.align-items-center.justify-content-center.col-10.col-sm-5
        figure
          img(src='@/assets/curso/tema4/img13.svg', alt='Texto que describa la imagen')
      .col-1
    .row.mb-4
      .col-1
      .col-10
        p Estos dos procesos pueden llegar a combinarse y siempre depender de la idea inicial que se tenga respecto a la prenda que se pretende realizar.
      .col-1
 
